.section {
    background-color: hsl(0, 0%, 100%);
    padding: 0px 15px 15px;
    margin: 10px 0;
}

.section__header {
    display: grid;
    grid-template-columns: auto auto;
    grid-gap: 20px;
    justify-content: space-between;
    align-items: center;
    padding: 20px;
    border-bottom: 1px solid hsl(0, 0%, 95%);
}

@media(max-width: 757px) {
    .section__header {
        grid-template-columns: 1fr;
    }
}

.section__body {
    padding: 20px;
}

